import React, { useEffect, useState } from 'react';
import { graphql } from 'gatsby';
import { Helmet } from 'react-helmet';
import ReactGA from 'react-ga';
import nodeFetch from 'node-fetch';
import loadable from '@loadable/component';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Layout from '../components/layout';
import Section from '../components/section';
// import OpeningAnimation from '../components/OpeningAnimation';
import NavbarSection from '../components/sections/Navbar';
import MainvisualSection from '../components/sections/Mainvisual';
import defaultData from '../components/defaultData';
import DarumaIcon from '../components/svgs/DarumaIcon';

import {
  HandshakeIcon,
  WrapTheHouseIcon,
  RentTheHouseIcon,
  CleanTheHouseIcon,
  WrapTheHeartIcon
} from '../components/svgs/ServiceIcons';

const serviceIcons = [
  HandshakeIcon,
  WrapTheHouseIcon,
  RentTheHouseIcon,
  CleanTheHouseIcon,
  WrapTheHeartIcon
];

const IndexPage = ({ data: { site: siteMetadata } }) => {
  const Company = loadable(() => import('../components/sections/company'));
  const Contact = loadable(() => import('../components/sections/contact'));
  const Footer = loadable(() => import('../components/footer'));

  const [aboutData, setAboutData] = useState([
    {
      title: '',
      description: ''
    }
  ]);
  const [serviceData, setServiceData] = useState([]);
  const [companyData, setCompanyData] = useState([]);

  const googleSheetsResponseToObject = res => {
    const columns = res.values.shift();

    const datas = res.values.map(fields => {
      const data = fields.reduce((obj, val, i) => {
        const key = columns[i];
        obj[key] = val;
        return obj;
      }, {});

      return data;
    });

    return datas;
  };

  useEffect(() => {
    const fetch = async (sheetName, range) => {
      try {
        const endpoint = 'https://sheets.googleapis.com/v4/spreadsheets/';
        const spreadsheetId = '17m-7BWL_46Cy6jsHE-VKJmmJQdHMrbu_Hdh7q3g43l8';
        const apiKey = 'AIzaSyCyflg5eN9uMwbBUbhugW7KbXwbmM93en8';
        const resource = `/values/${sheetName}${range}?key=${apiKey}`;
        const apiUrl = endpoint + spreadsheetId + resource;

        const res = await nodeFetch(apiUrl);

        if (!res || !res.ok) throw new Error();

        const data = await res.json();

        return googleSheetsResponseToObject(data);
      } catch (e) {
        return defaultData.sections[sheetName].contents;
      }
    };

    ReactGA.initialize('UA-189752134-1');
    ReactGA.pageview(window.location.pathname + window.location.search);

    const fetchData = async () => {
      setAboutData(await fetch('about', '!A1:B'));
      setServiceData(await fetch('service', '!A1:C'));
      setCompanyData(await fetch('company', '!A1:C'));
    };

    fetchData();
  }, []);

  const theme = createTheme({
    typography: {
      fontSize: 12,
      fontFamily: [
        '"Noto Sans JP"',
        '"Helvetica Neue"',
        'Arial',
        '"Hiragino Kaku Gothic ProN"',
        '"Hiragino Sans"',
        'Meiryo',
        'sans-serif'
      ].join(','),
      h2: {
        textAlign: 'center',
        fontSize: 28,
        fontWeight: 500,
        letterSpacing: 1.4
      },
      h5: {
        fontSize: 18,
        fontWeight: 700,
        lineHeight: 1.8
      },
      h6: {
        fontSize: 14,
        fontWeight: 500
      },
      body2: {
        fontSize: 11
      }
    }
  });

  return (
    <ThemeProvider theme={theme}>
      <Layout>
        <Helmet>
          <html lang={siteMetadata.lang} />
          <meta name='description' content={siteMetadata.description} />
          <title>{siteMetadata.title}</title>
          <link rel='canonical' href={siteMetadata.siteUrl} />
          <meta property='og:title' content={siteMetadata.title} />
          <meta property='og:type' content='website' />
          <meta property='og:url' content={siteMetadata.siteUrl} />
          <meta property='og:description' content={siteMetadata.description} />
          <meta property='og:locale' content={siteMetadata.locale} />
          <meta property='og:site_name' content={siteMetadata.siteName} />
          <meta property='og:image' content={siteMetadata.ogpUrl} />
          <meta property='og:image:secure_url' content={siteMetadata.ogpUrl} />
          <meta property='og:image:type' content='image/png' />
          <meta property='og:image:width' content='1200' />
          <meta property='og:image:height' content='630' />
          <meta property='og:image:alt' content={siteMetadata.siteName} />
          <meta name='twitter:card' content='summary_large_image' />
          <meta name='twitter:title' content={siteMetadata.title} />
          <meta name='twitter:description' content={siteMetadata.description} />
          <meta name='twitter:image' content={siteMetadata.ogpUrl} />
        </Helmet>
        <MainvisualSection />
        <NavbarSection />
        <Section id='about'>
          <Grid container justifyContent='space-evenly' spacing={4}>
            <Grid item xs={12} md={4} lg={6}>
              <Box
                display='flex'
                alignItems='center'
                justifyContent='center'
                sx={{ height: '100%' }}
              >
                <Box
                  sx={{
                    width: 180
                  }}
                >
                  <DarumaIcon />
                </Box>
              </Box>
            </Grid>
            <Grid item xs={12} md={8} lg={6}>
              <Box>
                <Typography
                  variant='h5'
                  letterSpacing={1.5}
                  dangerouslySetInnerHTML={{
                    __html: aboutData[0].title
                  }}
                ></Typography>
                <Typography
                  letterSpacing={1.5}
                  sx={{
                    mt: 3,
                    lineHeight: 1.8
                  }}
                >
                  {aboutData[0].description}
                </Typography>
              </Box>
            </Grid>
          </Grid>
        </Section>
        <Section id='service'>
          <div className='service-background'>
            <div className='row'>
              {serviceData.map(({ color, title, description }, i) => {
                const ServiceIcon = serviceIcons[i];

                return (
                  <div key={i} className='col-lg-6'>
                    <div
                      className={`mb-5 p-4 rounded service-item-${i + 1}`}
                      style={{
                        borderStyle: 'solid',
                        borderWidth: '2px',
                        borderColor: '#e9e9e9',
                        boxSizing: 'border-box',
                        letterSpacing: '1.4px',
                        lineHeight: '1.8rem'
                      }}
                    >
                      <div
                        className='row'
                        style={{
                          minHeight: '120px'
                        }}
                      >
                        <div className='col-9'>
                          <Typography mb={4} fontSize={14} color='#fff'>
                            <Box component='span' p={0.5} bgcolor={color}>
                              {title}
                            </Box>
                          </Typography>
                          <Typography>{description}</Typography>
                        </div>
                        <div className='col-3 d-flex align-items-center'>
                          <ServiceIcon
                            className='d-block mx-auto my-0'
                            style={{
                              stroke: color,
                              width: '100%',
                              maxWidth: '80px',
                              maxHeight: '80px'
                            }}
                          />
                        </div>
                      </div>
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        </Section>
        <Company data={companyData} />
        <Contact />
        <Footer />
      </Layout>
    </ThemeProvider>
  );
};

export const pageQuery = graphql`
  query IndexQuery {
    site {
      siteMetadata {
        lang
        locale
        title
        siteName
        description
        siteUrl
        ogpUrl
      }
    }
  }
`;

export default IndexPage;
